import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { OpportunityComponent } from './opportunity.component';
import { opportunityRouting } from './opportunity.routing';
import { TextEditorModule } from '../../util/text-editor/text-editor.module';
import { MatButtonModule, MatIconModule } from '@angular/material';
import { DomFlexContainerModule } from '@app/util/dom-flex-container/dom-flex-container.module';


@NgModule({
  imports: [
    CommonModule,
    opportunityRouting,
    MatButtonModule,
    MatIconModule,
    DomFlexContainerModule,
    TextEditorModule
  ],
  declarations: [OpportunityComponent]
})
export class OpportunityModule { }



